<!---------------    -------------------->
<template>
  <div class="container-fluid" >
    <div  class="itemlist-top" >
      <!---------------    -------------------->
      <b-row>
        <div class="col-sm-4">
            <ul class="itemlist-ul">
              <li class="itemlist-li">
                >
              </li>
              <li class="itemlist-li">
                {{FilterBreadcrumb}}
              </li>
              <li class="itemlist-li" v-if="SortBreadcrumbVisible">
                >{{SortBreadcrumb}}
              </li >
            </ul>
        </div>
        <div class="col-sm-4">
          <b-button-group class="itemlist-button-group">
            <b-button variant="transparent" @click="onSort(1)"><span class="top-text"></span></b-button>
            <b-button variant="transparent" @click="onSort(2)"><span class="top-text"></span></b-button>
            <b-dropdown variant="transparent" right>
              <template #button-content>
                <span class="top-text"></span>
              </template>
                <b-dropdown-item @click="onSort(3)" ></b-dropdown-item>
                <b-dropdown-item @click="onSort(4)"></b-dropdown-item>
            </b-dropdown>
            <b-dropdown variant="transparent" right>
              <template #button-content>
                <span class="top-text"></span>
              </template>
                <b-dropdown-item  @click="onSort(5)"></b-dropdown-item>
                <b-dropdown-item  @click="onSort(6)"></b-dropdown-item>
            </b-dropdown>
          </b-button-group>
        </div>
        <div class="col-sm-4">
          <b-input-group
            class="itemlist-input-group"
            @change="onSort(7)"
            size="sm"
          >
            <b-form-input class="input" v-model="searchString"></b-form-input>
            <b-input-group-append >
              <b-button @click="onSort(7)" size="sm" variant="transparent">
                  <b-icon :icon="'search'"></b-icon>
                </b-button>
            </b-input-group-append>
          </b-input-group>
        </div>
      </b-row>
    </div>
    <div  class="itemlist-content">
      <!---------------    -------------------->
      <b-row>
        <b-card-group id="itemlist" class="col-md-3"  v-for="items in filterlist[currentPage-1]" :key="items.productno" deck>
          
            
            <b-card
              id="card"
              no-body
              bg-variant="transparent"
              class="border-0"
              >
              <router-link :to="{name:'item',params:{id:items.productno}}">
                <b-card-img :src="items.imagesrc" img-alt="" img-top></b-card-img>
              </router-link>
                <b-card-body>
                  <p>{{items.producttitle}}
                  <a class="card-text">${{items.productprice}}</a></p>
                  <b-button class="CartAddButton" v-on:click="CartClick(items)" variant="danger">
                    
                  </b-button>
                </b-card-body>
            </b-card>
        </b-card-group>
      </b-row>
    </div>
    <b-pagination
      v-model="currentPage"
      :total-rows="rows"
      :per-page="perPage"
      aria-controls="card"
      class="pageController"
    ></b-pagination>
  </div>
</template>

<script>
export default {
  data(){
      return{
        // ---routere------
        items:[], 
         //-- pagination 
        currentPage:'1',// 
        perPage: 8,     // Card 
        // (rows  / perPage  ) = 
        
        //--itemlist 
        sortMethod:'', // itemlist  => 
        searchString:'',
        SortBreadcrumbVisible:false,
        FilterBreadcrumb:'',
        SortBreadcrumb:''
      }
    },watch:{
      $route(now){
        if(this.$route.params.str=='login'){
            this.$root.$emit('bv::toggle::modal', 'modal')
          }
      },
      '$store.state.filterString.itemNo'(){
        if(this.$store.state.filterString.itemNo!='99'){//
          this.FilterBreadcrumb = this.$store.state.filterString.itemName
        }else{
          this.SortBreadcrumbVisible = false
          this.FilterBreadcrumb = ''
          this.SortBreadcrumb = ''
          this.sortMethod = ''
        }
      }
    } ,mounted() {
        this.$http.get('https://zchenapi.herokuapp.com/Product/GetProduct')
        .then(response => {
            this.items = response.data;
        });
    },computed:{
      rows(){ // 
        var sum=0
        this.filterlist.forEach((value, index) => {
          sum =sum + value.length 
        });
        return sum
      },
      /*
      itemNo:99,itemName:'
      itemNo:2,itemName:'
      itemNo:3,itemName:'
      itemNo:4,itemName:'
      itemNo:5,itemName:'
      itemNo:6,itemName:'
      itemNo:7,itemName:'
      itemNo:66,itemName:'
      itemNo:88,itemName:'
      * */

      filterlist(){ // LIST
        if(this.$store.state.filterString.itemNo=='99'){   //
          return this.initList(this.initSort(this.items))

        }else if(this.$store.state.filterString.itemNo=='66'){ //
          var filterlist = this.items.filter(item => {  
            return item.onsale===1
          })
          return this.initList(this.initSort(filterlist))

        }else if(this.$store.state.filterString.itemNo=='88'){
            //
            console.log('')
            return this.initList(this.initSort(this.items))
        }else{      
          var filterlist = this.items.filter(item => { // 
            return item.categoryno=== this.$store.state.filterString.itemNo 
          })
          return this.initList(this.initSort(filterlist)) //  
        }
      }
    },methods:{
      CartClick(items){ //
        if(this.$store.state.items.some(item => item.productno === items.productno)){
            this.$root.$bvToast.toast(
              `${items.producttitle}`,
              {
                title: "",
                autoHideDelay: 2000,
                appendToast: true
              }
            );
            var i = this.$store.state.items.findIndex(x => x.productno === items.productno);
            this.$store.commit("cartDelete",i)

        }else{
          this.$root.$bvToast.toast(`${items.producttitle}`, {
            title: "",
            autoHideDelay: 2000,
            appendToast: true
          });
          this.$store.commit({
              type:"cartInsert", 
              ProductNo: items.productno,
              ProductTitle:items.producttitle,
              ProductSpecification:'',
              ProductMeasurement:'',
              ProductPrice:items.productprice
          })
        }
      },
       //--  items ------
      initList(oldList){
        var newList = []
        var templist=[] // PUSH  LIST   
        var cnt=7 //   cnt + 1 
        oldList.forEach((value, index) => {
            templist.push(value)
            if((index%cnt==0&index!=0)|oldList.length==1|index==oldList.length-1){
              newList.push(templist)
              templist=[]
            }
        });
        return newList
      },
      // itemlist 
      onSort(s){
        this.sortMethod = s
      },
      initSort(oldList){
        //    Visible
        if(this.sortMethod>0&&this.sortMethod<8){
          this.SortBreadcrumbVisible = true
        }
        
        var key 
        if(this.sortMethod==1){        //
          this.SortBreadcrumb = ''
          return oldList.filter(item => { 
            key = 'sold'
            return oldList.sort(function(a,b){
              var x = parseInt(a[key]);
              var y = parseInt(b[key]);
              return x<y?1:-1
            })
          })
        }else if(this.sortMethod==2){  //
          this.SortBreadcrumb = ''
          return oldList.filter(item => { 
            return item.Recommend===1
          })
        }else if(this.sortMethod==3){  //
          this.SortBreadcrumb = ''
          key = 'productprice'
          return oldList.sort(function(a,b){
            var x = parseInt(a[key]);
            var y = parseInt(b[key]);
            return x>y?1:-1
          })
        }else if(this.sortMethod==4){ //
          this.SortBreadcrumb = ''
          key = 'productprice'
          return oldList.sort(function(a,b){
            var x = parseInt(a[key]);
            var y = parseInt(b[key]);
            return x<y?1:-1
          })
        }else if(this.sortMethod==5){  //
          this.SortBreadcrumb = ''
          key = 'ondate'
          return oldList.sort(function(a, b) {
            var x = new Date(b[key]) 
            var y = new Date(a[key])
            return x>y?1:-1
          })
        }else if(this.sortMethod==6){ //
          this.SortBreadcrumb = ''
          key = 'ondate'
          return oldList.sort(function(a, b) {
            var x = new Date(b[key]) 
            var y = new Date(a[key])
            return x<y?1:-1
          })
        }else if(this.sortMethod==7){
          return oldList.filter(
            item => {return (item.producttitle.toLowerCase().indexOf(this.searchString.toLowerCase()) > -1) | (item.productsubtitle.indexOf(this.searchString) > -1)
          })
        }else{
          this.sortMethod=''
          return oldList
        }
      }
    }
}
</script>
<style>
/*  */
.itemlist-top{
  width: 100%;
  margin: 0 auto;
  padding: 1vh;
  border-radius: 10px;
  background-color: #3490C5;
}
.itemlist-ul{
  width: 100%;
  display:inline;
  color: #eee;
}
.itemlist-li{
  display:inline-block;
}
.itemlist-button-group{
  width: 100%;
  margin: 0;
  padding: 0;
  /* margin-right: 3vh; */
}
.itemlist-input-group{
  width: 100%;
  margin: 0;
  padding: 0;
  /* margin-right: 3vh; */
}
.itemlist-input-group .input{
  background-color: transparent;
  color:#eee;
}
.itemlist-top .top-text{
  color: #eee;
}
/*  */
.itemlist-content{
  width:95%;
  margin: auto;
}
#itemlist{
  margin: auto;
  margin-top: 3vh;
}
/* CARD */
.card-text{
  color: red;
}
.CartAddButton{
  height: 5vh;
  width: 100%;
}
/*  */
.pageController{
  width:30vh;
  margin:auto;
  margin-top: 3vh;
  margin-bottom: 3vh;
}
</style>


