<template>
    <div class="sidebar">
        <div class="sideHeader">

        </div>
        <div class="sideContent">
            <b-list-group>
                <b-list-group-item 
                v-for="item in  items"
                :key="item.itemNo"
                @click="itemSelected(item)"
                href="#"
                v-b-toggle.sidebar>
                {{item.itemName}}
                </b-list-group-item>
            </b-list-group>
        </div>
        
    </div>
</template>
<script>
export default {
    data() {
        return {
            items:[
                {itemNo:99,itemName:''},
                {itemNo:2,itemName:''},
                {itemNo:3,itemName:''},
                {itemNo:4,itemName:''},
                {itemNo:5,itemName:''},
                {itemNo:6,itemName:''},
                {itemNo:7,itemName:''},
                {itemNo:66,itemName:''},
                {itemNo:88,itemName:''},
            ]
        }
    },
    computed:{

    },
    methods: {
        itemSelected(i){
            this.$router.push({
                name:'itemlist'
            }).catch(err => {})
            this.$store.commit("setFilterString",i)
        }
    },
}
</script>
<style>
    a, a:link, a:visited{
        text-decoration: none;
        font-weight: bold;
    }
    #sidebar a:hover{
        color:#eee;
        background-color: #3490C5;
        text-decoration: none;
    }
    .sidebar{
        height: inherit; /**/
        /*flex--headercenterfooter*/
        display: flex;
        flex-direction: column;
    }
    .sideHeader{
        height: 20vh;
        background-color: #3490C5;
    }
    .sideContent{
        flex: 1;
        background-color: #eee;
    }
</style>


