<!---------------    -------------------->
<template>
  <div class="container-fluid" >
    <!---------------    -------------------->
    <b-breadcrumb style="margin-top:20px;">
      <b-breadcrumb-item>
        <router-link to="/" >
          <b-icon icon="house-fill" scale="1.25" shift-v="1.25" aria-hidden="true"></b-icon>
            
        </router-link>
      </b-breadcrumb-item>
      <b-breadcrumb-item active></b-breadcrumb-item>
    </b-breadcrumb>
      <div class="item">
        <b-row>
          <!---------------    -------------------->
          <div class="col-md-6">
            <b-carousel
              id="carousel"
              controls
              indicators
              no-hover-pause
            >
              <b-carousel-slide
                v-for="(image,index) in images"
                :key="index"
              >
                <template v-slot:img>
                  <img
                  class="carousel-image"
                  :src="image.imagesrc"
                  alt="image slot">
                </template>
              </b-carousel-slide>
            </b-carousel>
          </div>
          <!---------------     -------------------->
          <div class="col-md-6">
            <div class="info">
              <b-row class="info-row" cols="2">
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6"></b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6">{{items.productno}}</b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6"></b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6">{{items.producttitle}}</b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6"></b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6"><a style="color:red;">${{items.productprice}}</a></b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6"></b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6">
                  <b-form-select 
                    v-model="specificationselected" 
                    size="sm" 
                    @change="specificationChange"
                    style="width:10vh;"
                    class="mt-3">
                    <b-form-select-option  :value="i" v-for="(s,i) in specification" :key=i>
                      {{s.itemSpecification}}
                    </b-form-select-option>
                  </b-form-select>
                </b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6"></b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6">
                  <b-form-select 
                    v-model="measurementselected" 
                    size="sm" 
                    style="width:10vh;"
                    class="mt-3">
                    <b-form-select-option :value="m.itemMeasurement" v-for="(m,j) in measurement" :key=j>
                      {{m.itemMeasurement}}
                    </b-form-select-option>
                  </b-form-select>
                </b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6"></b-col>
                <b-col class="info-col"  cols-sm="2" cols-md="4" cols-lg="6">
                  <b-form-spinbutton 
                    id="sb-inline" 
                    v-model="quantity" 
                    min="1" max="100"
                    size="sm">
                  </b-form-spinbutton>
                  </b-col>
              </b-row>
              <hr>
              <center>
                <b-button  style="width:100%;" variant="danger" v-on:click="cartClicked"></b-button>
              </center>                
            </div>
          </div>  
        </b-row>
      </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      // ---routere(ProductNo)------
      productNo:this.$route.params.id,  
      items:'', 
      // -------------ProductNo  Item 
      images:[], // ProductNo  Image 
      specificationselected: null,  //  selected
      specification: [], // Item 
      measurementselected: null, //  selected
      measurement: [], // Item 
      quantity:1, // 
    }
  },mounted(){
    //   ProductNo 
    this.$http.get(`https://zchenapi.herokuapp.com/Product/GetProduct?no=${this.productNo}`)
      .then(response => {
        this.items = response.data[0]
      });
    //    ProductNoItem  Image 
    this.$http.get(`https://zchenapi.herokuapp.com/Product/GetProductImage?no=${this.productNo}`)
      .then(response => {
        this.images = response.data;
      });  
    //   ProductNoItem
    this.$http.get(`https://zchenapi.herokuapp.com/Product/GetItem?no=${this.productNo}`)
      .then(response => {
        this.specification = response.data;
      });
  },computed:{
    
  },methods:{
     //    Item 
    specificationChange(i){
        this.measurement = this.specification[i].measurements
    },
    cartClicked(){
      if(this.$store.state.items.some(item => item.productno === this.items.productno)){
            this.$root.$bvToast.toast(
              `${this.items.producttitle}`,
              {
                title: "",
                autoHideDelay: 2000,
                appendToast: true
              }
            );
            var i = this.$store.state.items.findIndex(x => x.productno === this.items.productno);
            this.$store.commit("cartDelete",i)
      }else{
        this.$root.$bvToast.toast(`${this.items.producttitle}`, {
            title: "",
            autoHideDelay: 2000,
            appendToast: true
          });
        this.$store.commit({
            type:"cartInsert", 
            ProductNo: this.items.productno,
            ProductTitle:this.items.producttitle,
            ProductPrice:this.items.productprice,
            ProductQantity:this.quantity,
            ProductSpecification:(this.specificationselected!=null)?this.specification[this.specificationselected].itemSpecification:null,
            ProductMeasurement:(this.measurementselected)?this.measurementselected:null
        })
      }
    },
  }
}
</script>
<style>
.item{
  padding: 20px;
  max-height: 100%;
  max-width: 100%;
  margin:auto;
  height:inherit; /**/
  display: flex;
}
#carousel{
  width: 100%; 
  height: 100%; 
  max-width:  100%;
  display:  flex;
  align-items: center;
  justify-content:  center;
  overflow: hidden;
  padding:30px;
  
}
.carousel-image{
  max-width: 100%; 
  max-height: 100%; 
}
.info{
  margin-top: 50px;
  padding:10px;
}

.info .info-col{
  padding:10px;
  font-size: 1em;
  font-weight: bold;
  display:  flex;
  align-items: center;
  justify-content:  center;
}

</style>


